/* Auth.Js */

import axios from 'axios';
import React, { useState } from 'react';
import logo from './assets/img/itb-text.png';

import './index.css';
import './assets/css/sb-admin-2.min.css';
import './assets/vendor/fontawesome-free/css/all.min.css';

function Auth (props) {

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const login = () => {

        const url = ('http://sancaliar82.pythonanywhere.com/auth/obtain-token');
        const setAuthtoken = (token, user) => {sessionStorage.setItem('token', token);
            sessionStorage.setItem('user', user)};

        axios
            .post (url, {username: username, password: password})
            .then (response => {setAuthtoken(response.data.token, response.data.user);
                props.history.push('/');
            })
    };

    return (
        <div className="bg-pattern-midnight container-fluid py-4">
            <div className="row justify-content-center">

                <div className="col-md-8 col-lg-5">
                    <div className="card shadow border-0 bg-gradient-light o-hidden">

                        <div className="card-body">
                            <div className="col p-5 mx-auto">
                                <div className="row mb-5 justify-content-center">
                                    <img className="col-9 img-fluid" src={logo} alt=""/></div>
                                
                                {/* <!-- Method - Action --> */}
                                <form className="user">
                                    <div className="form-group">
                                        <input className="form-control form-control-user" type="text" value={username} onChange={(e)=>setUsername (e.target.value)} placeholder="Enter Your Username" required="required"/></div>
                                    <div className="form-group">
                                        <input className="form-control form-control-user" type="password" value={password} onChange={(e)=>setPassword (e.target.value)} placeholder="Password" required="required"/></div>

                                    <button type="button" className="btn btn-primary btn-user btn-block" onClick={login}>
                                        <span className="font-weight-bold">LOGIN</span>
                                    </button>
                                </form>

                                {/* <!-- Dropdown Divider --> */}
                                <div className="d-block dropdown-divider my-4"></div>

                                <div className="row justify-content-center">
                                    <a className="text-center text-primary font-weight-bold small" href="">
                                        Forgot Password?
                                    </a>
                                </div>
                            </div>
                        {/* <!-- End of card content --> */}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Auth;