import React from 'react';
import Main from './Main';
import Auth from './Auth';

import { Switch } from "react-router-dom";
import { Public, Private } from './assets/js/utils/AccessRoute';
import { AuthProvider } from './assets/js/contexts/AuthContext';

function App() {
    return (
        <div>
            <AuthProvider>
                <Switch>
                    <Public exact path="/auth" component={Auth} />
                    <Private exact path="/" component={Main} />
                </Switch>
            </AuthProvider>
      </div>
    );
};

export default App;
